import common from '@/utils/common.js';

import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"
import uniCollapse from '@/components/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue'
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let uid;
let custom_id;
export default {
	components: {
		faIcon,
		uniCollapse,
		loading,
		uniCollapseItem,
		uniNavBar,
		uniSegmentedControl
	},
	data() {
		return {
			showOptions: false,
			header: {
				total: 0,
				total_money: 0,
				get_money: 0,
			}, //头部的统计数据
			loading: true,
			items: ['今天', '昨天', '近七天', '近一个月'],
			list: [],
			current: 0,

			now_day: common.getDay(0, false),
			end_day: common.getDay(1, false),
			max_day: common.getDay(0, false),
		}
	},

	onLoad(options) {
		console.log(options)
		that = this;
		custom_id = options.id
		uid = uni.getStorageSync("uid");
		//custom_id = "0adcad2160"
		//that.get_list(options.id)
		that.get_list(custom_id)
		that.get_header_data(custom_id, 0)

	},
	methods: {
		option_confrim(){
			that.header = {
				total: 0,
				total_money: 0,
				get_money: 0,
			}
			that.showOptions = false;
			that.get_header_data(custom_id)
			that.get_list(custom_id)
		},

		//modal重置的确认点击
		option_reset() {
			that.header = {
				total: 0,
				total_money: 0,
				get_money: 0,
			}
			
			that.now_day = common.getDay(0, false),
			that.end_day = common.getDay(1, false),
			that.max_day = common.getDay(0, false),
			that.showOptions = false;
			that.get_header_data(custom_id)
			that.get_list(custom_id)
		},

		bindDateChange1(e) {
			that.now_day = e.detail.value;
		},

		bindDateChange2(e) {
			that.end_day = e.detail.value;
		},

		//日期筛选
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
				that.header = {
					total: 0,
					total_money: 0,
					get_money: 0,
				}
				if (index == 0) {
					that.now_day = common.getDay(0, false)
					that.end_day = common.getDay(1, false)
				} else if (index == 1) {
					that.now_day = common.getDay(-1, false)
					that.end_day = common.getDay(0, false)
				} else if (index == 2) {
					that.now_day = common.getDay(-7, false)
					that.end_day = common.getDay(1, false)
				} else if (index == 3) {
					that.now_day = common.getDay(-30, false)
					that.end_day = common.getDay(1, false)
				}
				
				that.get_header_data(custom_id)
				that.get_list(custom_id)
			}
		},

		//得到该客户的统计数据
		get_header_data(id) {
			const query = Bmob.Query("Bills");
			query.equalTo("userId", "==", uid);
			query.equalTo("type", "==", -1);
			query.equalTo("custom", '==', id);
			query.include("goodsId");
			query.equalTo("createdAt", ">=", that.now_day + ' 00:00:00');
			query.equalTo("createdAt", "<=", that.end_day + ' 00:00:00');
			query.find().then(res => {
				//console.log(res)

				for (let item of res) {
					//console.log(item)
					that.header.total += item.num
					that.header.total_money += Number(item.goodsId.costPrice) * item.num
						that.header.get_money += item.total_money - Number(item.goodsId.costPrice) * item.num
				}

			});
		},

		//得到该客户的销售列表
		get_list(id) {
			const query = Bmob.Query("order_opreations");
			query.equalTo("master", "==", uid);
			query.equalTo("type", '==', -1);
			query.equalTo("custom", '==', id);
			query.include("opreater");
			query.equalTo("createdAt", ">=", that.now_day + ' 00:00:00');
			query.equalTo("createdAt", "<=", that.end_day + ' 00:00:00');
			query.order("-createdAt");
			query.find().then(res => {
				console.log(res)
				let details = res
				for (let item of details) {
					item.relations = item.detail;	
				}
				that.list = details
				that.loading = false
				//console.log(details)
			});
		},
	}
}
